
.welcome_image {
    min-width: 100%;
    max-height: 770px;
    object-fit: cover;
}

.profile_image_div {
display: flex;
flex-direction: column;
justify-content: space-around;
justify-items: center;
align-content: center;
align-items: center;
    width: 200px;
    height: 200px;
    text-align: center;
}

.profile_image {
    height: 125px;
    width: 125px;
    border: 1px solid white;
        border-radius: 75px;
        background-color: rgba(255, 255, 255, 0.7);
animation: 7s infinite ease-in-out bounce;
    -webkit-animation: bounce 7s infinite ease-in-out;
    -moz-animation: bounce 7s infinite ease-in-out;
    -o-animation: bounce 7s infinite ease-in-out;
    -ms-animation: bounce 7s infinite ease-in-out;
}



.welcome_text {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: white;
    font-size: 15pt;
    font-weight: 600;
    width: 350px;
}

@-webkit-keyframes bounce {
	0% {-webkit-transform: translateY(0);}	
	50% {-webkit-transform: translateY(-10px);}
	100% {-webkit-transform: translateY(0px);}
}
 
@-moz-keyframes bounce {
	0% {-moz-transform: translateY(0);}
	50% {-moz-transform: translateY(-10px);}
	100% {-moz-transform: translateY(0px);}
}
 
@-o-keyframes bounce {
	0% {-o-transform: translateY(0);}
	50% {-o-transform: translateY(-10px);}
	100% {-o-transform: translateY(0px);}
}

@-ms-keyframes bounce {
    0% {-o-transform: translateY(0);}
	50% {-o-transform: translateY(-10px);}
	100% {-o-transform: translateY(0px);}
}

@keyframes bounce {
	0% {transform: translateY(0);}
	50% {transform: translateY(-10px);}
	100% {transform: translateY(0px);}
}




.hi {

        animation: 5s infinite ease-in-out bounce;
            -webkit-animation: bounce 5s infinite ease-in-out;
            -moz-animation: bounce 5s infinite ease-in-out;
            -o-animation: bounce 5s infinite ease-in-out;
            -ms-animation: bounce 5s infinite ease-in-out;
    }


.profile_text_div {

        position: absolute;
        top: 93%;
        left: 70%;
        transform: translate(-50%, -50%);
        height: 60px;
        padding: 0 40px;
        overflow: hidden;
    width: 235px;
}

.profile_inner {
    text-align: center;
    color: greenyellow;
    line-height: 60px;
    width: 200px;
}

.profile_inner:first-child {
    animation: rotate 10s infinite;
    -webkit-animation: rotate 10s infinite;
    -moz-animation: rotate 10s infinite;
    -o-animation: rotate 10s infinite;
    -ms-animation: rotate 10s infinite;
}


@-webkit-keyframes rotate {
	   0%{
        margin-top: 0px;
    }
    16%{
        margin-top: -60px;
    }
    33%{
        margin-top: -120px;
    }
    50%{
        margin-top: -180px;
    }
    66%{
        margin-top: -120px;
    }
    82%{
        margin-top: -60px;
    }
    100%{
        margin-top: 0px;
    }
}
 
