@media (min-width: 320px) and (max-width: 1279px) {

    /* Phone */
    .welcome_image {
        width: 320px;
        height: 420px;
    }

    .profile_text_div {
        width: 200px;
        font-size: 10pt;
        padding: 0;
        left: 67%;
    }

    .welcome_text {
        width: 320px;
    }

    .profile_con_text {
        width: 320px;
    }

    .profile_inner {
        width: 200px;
        font-size: 14pt;
    }
}